/**=====================
    84. cart CSS start
==========================**/
.cart {
  .total-amount {
    text-align: right !important;
    margin-top: 30px;
  }
  .amount-space {
    margin-left: 40px;
  }
  .cart-btn-transform {
    text-transform: capitalize;
  }
  .qty-box {
    width: 20%;
    margin: 0 auto;
    .input-group {
      .input-group-prepend{
        .btn{
          border-radius: 4px 0 0 4px;
        }
      }
      .input-group-append{
        .btn{
          border-radius: 0 4px 4px 0;
        }
      }
      .btn {
        background-color: #ddd !important;
        border-color: #ddd !important;
        padding: 5px 12px;
        
      }
    }
  }
}
.cart-buttons {
  margin-top: 30px;
  .cart-btn {
    float: right;
    padding-right: 85px;
  }
}
/**=====================
    84. cart CSS Ends
==========================**/
