/**=====================
    45. Dashboard CSS Start
==========================**/
.rain {
  background: white;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 0%,
    #ffffff 100%
  );
  height: 50px;
  position: absolute;
  width: 1px;
}
.cloud-rain {
  width: 500px;
  height: 500px;
  transform: rotate(45deg);
  position: absolute;
}
$rain: 150;

@for $i from 1 through $rain {
  $top: (random(50) + 50) * 1%;
  $left: random(100) * 1%;
  $opacity: (random(30) + 30) * 0.01;
  $delay: random(20) - 1s;

  .rain:nth-of-type(#{$i}) {
    animation-name: rain-#{$i};
    animation-delay: $delay;
    animation-duration: random(6) + 4s;
    animation-iteration-count: infinite;
    left: $left;
    opacity: $opacity;
    top: -$top;
  }

  @keyframes rain-#{$i} {
    0% {
      opacity: $opacity;
      left: $left;
      top: -$top;
    }
    100% {
      opacity: 0;
      top: $top + 40%;
    }
  }
}

.general-widget {
  .clock-card {
    border: 10px double rgba($white, 0.2);
    border-radius: 100%;
    position: absolute;
    top: -50px;
    right: 50px;
  }
  .mobile-clock-widget {
    border-radius: 40px;
  }
  .user-status {
    tbody {
      tr {
        &:hover {
          background-color: rgba($primary-color, 0.05);
          border-radius: 15px;
          transition: all 0.3s ease;
        }
      }
    }
  }
  .card {
    .card-header {
      .card-header-right {
        top: 27px;
      }
    }
  }
  .card-header {
    padding: 35px 50px;
  }
  .table {
    th {
      border-top: none;
      font-size: 16px;
      padding: 18px 0.75rem;
      border-bottom: 1px solid #dee2e6;
      border-top: 1px solid #dee2e6;
    }
    tr {
      &:last-child {
        td {
          border-bottom: none;
        }
      }
      td {
        border-bottom: 1px solid #f1f1f1;
        vertical-align: middle;
        padding: 0.85rem;
      }
      th,
      td {
        &:first-child {
          padding-left: 50px;
        }
        &:last-child {
          padding-right: 50px;
        }
      }
    }
  }
}
.cart-table {
  .table tr td {
    padding: 1rem;
  }
}

.welcome-popup {
  .modal-content {
    background-image: url("../../images/dashboard/popup.png");
    background-repeat: no-repeat;
    background-position: top center;
    border-radius: 30px;
    box-shadow: 2px 12px 26px 3px rgba(47, 46, 46, 0.3);
  }
  .btn-close {
    z-index: 9;
    position: absolute;
    background-color: $white;
    color: $primary-color;
    opacity: 1;
    border-radius: 15px;
    padding: 15px 15px;
    left: -20px;
    top: -10px;
    font-size: 10px;
    box-shadow: -2px 6px 11px 3px rgba($primary-color, 0.3);
  }
  .modal-header {
    height: 190px;
    border: none;
  }
  .contain {
    padding-top: 80px;
  }
}

.weather-widget {
  .card {
    overflow: hidden;
  }
  .weather-widget-top {
    height: 330px;
    background-image: url("../../images/dashboard/whather_bg.jpg");
    background-size: cover;
    border-radius: 40px 40px 0 0;
    .location {
      color: $white;
      position: absolute;
      top: 80px;
      right: 80px;
      i {
        font-size: 20px;
        position: absolute;
        right: 0;
      }
      h6 {
        margin-bottom: 0;
        padding-right: 25px;
      }
    }
    .media {
      height: 100%;
      .media-body {
        height: 100%;
        display: flex;
        align-items: center;
        h2 {
          font-size: calc(50px + (124 - 50) * ((100vw - 320px) / (1920 - 320)));
          color: $white;
          font-weight: 600;
          margin-bottom: 0;
          margin-top: 20px;
        }
      }
    }
    svg {
      width: calc(150px + (280 - 150) * ((100vw - 320px) / (1920 - 320)));
      height: calc(150px + (280 - 150) * ((100vw - 320px) / (1920 - 320)));
      .climacon_component-stroke {
        fill: $white;
        stroke-width: 0;
      }
    }
  }
  .weather-widget-bottom {
    padding: 0 30px;
    background-color: rgba($primary-color, 0.05);
    .row{
      justify-content: center;
    }
    .col-auto {
      padding: 0;
      .box {
        text-align: center;
        padding: 15px;
        position: relative;
        &:before {
          content: "";
          position: absolute;
          height: 100px;
          width: 100px;
          background-color: rgba($primary-color, 0.05);
          left: 0;
          top: 0;
          opacity: 0;
          transform: scale(0);
          transition: all 0.3s ease;
          border-radius: 100%;
        }
        &.active,
        &:hover {
          &:before {
            opacity: 1;
            transform: scale(1);
            transition: all 0.3s ease;
          }
        }
        h5,
        h6 {
          margin-bottom: 0;
        }
        h6 {
          opacity: 0.5;
        }
        svg {
          width: 70px;
          height: 70px;
          .climacon_component-stroke {
            fill: rgba($primary-color, 0.3);
            stroke: $primary-color;
          }
        }
      }
    }
  }
}

@each $btn-shadow-name, $btn-shadow-color in (primary, $primary-color),
  (secondary, $secondary-color), (success, $success-color),
  (danger, $danger-color), (info, $info-color), (light, $light-color),
  (dark, $dark-color), (warning, $warning-color)
{
  .btn-shadow-#{$btn-shadow-name} {
    background-color: $btn-shadow-color;
    border-radius: 25px;
    color: $white;
    box-shadow: 2px 12px 26px 3px rgba($btn-shadow-color, 0.2) !important;
    &:hover,
    &:active,
    &:not([disabled]):not(.disabled):active {
      color: $white;
      background-color: darken($btn-shadow-color, 10%);
      border-color: darken($btn-shadow-color, 10%);
    }
  }
}
@each $recent-images-name, $recent-images-color in (primary, $primary-color),
  (secondary, $secondary-color), (success, $success-color),
  (danger, $danger-color), (info, $info-color), (light, $light-color),
  (dark, $dark-color), (warning, $warning-color)
{
  .recent-images-#{$recent-images-name} {
    img {
      filter: drop-shadow(2px 3px 9px rgba($recent-images-color, 0.4));
    }
  }
}

