          text-decoration: none;
          font-family: $font-nunito, $font-serif;
        }
      }
    }
    .react-calendar__month-view__days__day,
    .react-calendar__navigation__label {
      font-weight: 700;
    }
    .react-calendar__tile {
      padding: 18px 4px;
      border-radius: 40%;
      &:enabled {
        &:focus,
        &:hover,
        &:active {
          background-color: $primary-color;
